@import url(https://fonts.googleapis.com/css2?family=Arvo:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: 'Nunito Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background{background-size:cover;background-repeat:no-repeat;background-position:center center;overflow:hidden;will-change:transform;-webkit-backface-visibility:hidden;backface-visibility:hidden;height:130vh;position:fixed;width:100%;-webkit-transform:translateY(30vh);transform:translateY(30vh);transition:all 1.3s cubic-bezier(0.22, 0.44, 0, 1)}.background:before{content:'';position:absolute;width:100%;height:100%;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,0.01)}.background:first-child{-webkit-transform:translateY(-15vh);transform:translateY(-15vh)}.background:first-child .content-wrapper{-webkit-transform:translateY(15vh);transform:translateY(15vh)}.background:nth-child(1){z-index:3}.background:nth-child(2){z-index:2}.background:nth-child(3){z-index:1}.splash-wrapper{display:flex;flex-direction:column;justify-content:center}.content-wrapper{height:100vh;display:flex;justify-content:center;text-align:center;flex-flow:column nowrap;color:#dededc;font-family:Arvo;text-transform:uppercase;-webkit-transform:translateY(40vh);transform:translateY(40vh);will-change:transform;-webkit-backface-visibility:hidden;backface-visibility:hidden;transition:all 1.8s cubic-bezier(0.22, 0.44, 0, 1)}.content-title{font-size:9vh;line-height:1.4;margin:0;opacity:1}.events-wrapper{display:flex;justify-content:space-around;align-items:baseline;text-align:center}.background.up-scroll{-webkit-transform:translate3d(0, -15vh, 0);transform:translate3d(0, -15vh, 0)}.background.up-scroll .content-wrapper{-webkit-transform:translateY(15vh);transform:translateY(15vh)}.background.up-scroll+.background{-webkit-transform:translate3d(0, 30vh, 0);transform:translate3d(0, 30vh, 0)}.background.up-scroll+.background .content-wrapper{-webkit-transform:translateY(30vh);transform:translateY(30vh)}.background.down-scroll{-webkit-transform:translate3d(0, -130vh, 0);transform:translate3d(0, -130vh, 0)}.background.down-scroll .content-wrapper{-webkit-transform:translateY(40vh);transform:translateY(40vh)}.background.down-scroll+.background:not(.down-scroll){-webkit-transform:translate3d(0, -15vh, 0);transform:translate3d(0, -15vh, 0)}.background.down-scroll+.background:not(.down-scroll) .content-wrapper{-webkit-transform:translateY(15vh);transform:translateY(15vh)}

